<template>
  <div class="player" v-show="playlist.length>0">
		<transition name="normal"
								@enter="enter"
								@afterEnter="afterEnter"
								@leave="leave"
								@afterLeave="afterLeave">
			<div class="normal-player" v-show="fullScreen">
				<div class="background">
					<img :src="currentSong.image" width="100%" height="100%">
				</div>
				<div class="top">
					<div class="back" @click="back">
						<i class="icon-back"></i>
					</div>
					<h1 class="title" v-html="currentSong.name"></h1>
					<h2 class="subtitle" v-html="currentSong.singer"></h2>
				</div>
				<div class="middle"
						 @touchstart.prevent="middleTouchStart"
						 @touchmove.prevent="middleTouchMove"
						 @touchend="middleTouchEnd">
					<div class="middle-l" ref="middleL">
						<div class="cd-wrapper" ref="cdWrapper">
							<div class="cd" :class="cdCls">
								<img :src="currentSong.image" class="image">
							</div>
						</div>
						<div class="playing-lyric-wrapper">
							<div class="playing-lyric">{{playingLyric}}</div>
						</div>
					</div>
					<scroll class="middle-r" ref="lyricList" :data="currentLyric && currentLyric.lines">
						<div class="lyric-wrapper">
							<div v-if="currentLyric">
								<p class="text" :class="{'current': index === currentLineNum}" ref="lyricLine" v-for="(line, index) in currentLyric.lines">{{line.txt}}</p>
							</div>
						</div>
					</scroll>
				</div>
				<div class="bottom">
					<div class="dot-wrapper">
						<span class="dot" :class="{'active': currentShow==='cd'}"></span>
						<span class="dot" :class="{'active': currentShow==='lyric'}"></span>
					</div>
					<div class="progress-wrapper">
						<span class="time time-l">{{format(currentTime)}}</span>
						<div class="progress-bar-wrapper">
							<progress-bar :percent="percent" @percentChange="onProgressBarChange"></progress-bar>
						</div>
						<span class="time time-r">{{format(currentSong.duration)}}</span>
					</div>
					<div class="operators">
						<div class="icon i-left" @click="changeMode">
							<i :class="iconMode"></i>
						</div>
						<div class="icon i-left" :class="disableCls">
							<i @click="prev" class="icon-prev"></i>
						</div>
						<div class="icon i-center" :class="disableCls">
							<i @click="togglePlaying" :class="playIcon"></i>
						</div>
						<div class="icon i-right" :class="disableCls">
							<i @click="next" class="icon-next"></i>
						</div>
						<div class="icon i-right">
							<i class="icon" :class="getFavoriteIcon(currentSong)" @click="toggleFavorite(currentSong)"></i>
						</div>
					</div>
				</div>
			</div>
		</transition>
		<transition name="mini">
	  	<div class="mini-player" v-show="!fullScreen" @click="open">
	  		<div class="icon">
	  			<img :class="cdCls" :src="currentSong.image" width="40" height="40">
	  		</div>
	  		<div class="text">
	  			<h2 class="name" v-html="currentSong.name"></h2>
	  			<p class="desc" v-html="currentSong.singer"></p>
	  		</div>
	  		<div class="control">
	  			<progress-circle :radius="radius" :percent="percent">
		  			<i @click.prevent.stop="togglePlaying" :class="miniIcon" class="icon-mini"></i>
	  			</progress-circle>
	  		</div>
	  		<div class="control" @click.stop="showPlaylist">
	  			<i class="icon-playlist"></i>
	  		</div>
	  	</div>
		</transition>
    <playlist ref="playlist"></playlist>
		<audio :src="currentSong.url" ref="audio" @play="ready" @error="error" @timeupdate="updateTime" @ended="end"></audio>
  </div>
</template>

<script>
	// 动画库
  import animations from 'create-keyframe-animation'
  // 处理歌词库
  import Lyric from 'lyric-parser'
  import {prefixStyle} from 'common/js/dom'
	import {mapGetters, mapMutations, mapActions} from 'vuex'
  import {playMode} from 'common/js/config'
	import ProgressBar from 'base/progress-bar/progress-bar'
	import ProgressCircle from 'base/progress-circle/progress-circle'
	import Scroll from 'base/scroll/scroll'
  import Playlist from 'components/playlist/playlist'
  import {playerMixin} from 'common/js/mixin'

	const transform = prefixStyle('transform')
	const transitionDuration = prefixStyle('transitionDuration')
  export default {
    mixins: [playerMixin],
  	data() {
  		return {
  			// 标识位
  			songReady: false,
  			currentTime: 0,
  			radius: 32,
  			// 当前歌词
  			currentLyric: null,
  			currentLineNum: 0,
  			// 标识cd或lyric显示
  			currentShow: 'cd',
  			playingLyric: ''
  		}
  	},
		// 不需要getter,seetter
  	created() {
  		this.touch = {}
  	},
  	computed: {
  		playIcon() {
  			return this.playing ? 'icon-pause' : 'icon-play'
  		},
  		miniIcon() {
  			return this.playing ? 'icon-pause-mini' : 'icon-play-mini'
  		},
  		cdCls() {
  			return this.playing ? 'play' : 'play pause'
  		},
  		disableCls() {
  			return this.songReady ? '' : 'disable'
  		},
  		percent() {
  			return this.currentTime / this.currentSong.duration
  		},
  		...mapGetters([
  			'fullScreen',
  			'playing',
  			'currentIndex'
  		])
  	},
  	methods: {
  		// 切换播放状态,要保证歌曲开始播放才能执行
  		togglePlaying() {
  			if (!this.songReady) {
  				return
  			}
  			this.setPlayState(!this.playing)
  			if (this.currentLyric) {
  				console.log('toggle')
  				this.currentLyric.togglePlay()
  			}
  		},
  		back() {
  			this.setFullScreen(false)
  		},
  		open() {
  			this.setFullScreen(true)
  		},
  		enter(el, done) {
  			const {x, y, scale} = this._getPosAndScale()

  			let animation = {
  				0: {
  					transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
  				},
  				60: {
  					transform: `translate3d(0,0,0) scale(1.1)`
  				},
  				100: {
  					transform: `translate3d(0,0,0) scale(1)`
  				}
  			}

  			animations.registerAnimation({
  				name: 'move',
  				animation,
  				presets: {
  					duration: 400,
  					easing: 'linear'
  				}
  			})

  			animations.runAnimation(this.$refs.cdWrapper, 'move', done)
  		},
  		afterEnter() {
  			animations.unregisterAnimation('move')
  			this.$refs.cdWrapper.style.animation = ''
  		},
  		leave(el, done) {
  			this.$refs.cdWrapper.style.transition = 'all 0.4s'
  			const {x, y, scale} = this._getPosAndScale()
  			this.$refs.cdWrapper.style[transform] = `translate3d(${x}px,${y}px,0) scale(${scale})`
  			// 完成后调用done
  			this.$refs.cdWrapper.addEventListener('transitionend', done)
  		},
  		afterLeave() {
  			// 取消动画
  			this.$refs.cdWrapper.style.transition = ''
  			this.$refs.cdWrapper.style[transform] = ''
  		},
  		prev() {
  			if (!this.songReady) {
  				return   				
  			}
  			if (this.playlist.length === 1) {
  				this.loop()
  			} else {
	  			let index = this.currentIndex - 1
	  			if (index === -1) {
	  				index = this.playlist.length - 1
	  			}
	  			this.setCurrentIndex(index)
  			}
  			this.songReady = false 				
  		},
  		next() {
  			// 只有audio缓冲好了才有效
  			if (!this.songReady) {
  				return   				
  			}
  			// 当列表只有一首歌时，index不会改变，无法进行下首播放，要自动调用loop
  			if (this.playlist.length === 1) {
          // 不会触发this.ready(),而songReady应一直为true
  				this.loop()
          return
  			} else {
	  			let index = this.currentIndex + 1
	  			if (index === this.playlist.length) {
	  				index = 0
	  			}
	  			// 修改currentIndex,currentsong也会发生改变
	  			this.setCurrentIndex(index)
	  			// 当前处于暂停状态的，则改为播放状态
	  			if (!this.playing) {
	  				this.togglePlaying()
	  			}
  			}
  			this.songReady = false
  		},
      // 当媒介就绪开始播放时
  		ready() {
  			this.songReady = true
        this.savePlayHistory(this.currentSong)
  		},
  		// 保证正常运行
  		error() {
  			this.songReady = true
  		},
  		updateTime(e) {
  			// 获取当前播放位置时间
  			this.currentTime = e.target.currentTime
  		},
  		format(interval) {
  			// 向下取整
  			interval = interval | 0
  			const minute = interval / 60 | 0
  			const second = this._pad(interval % 60, 2)
  			return `${minute}:${second}`
  		},
  		// 进度条改变时
  		onProgressBarChange(percent) {
  			const currentTime = this.currentSong.duration * percent
  			this.$refs.audio.currentTime = currentTime
  			if (!this.playing) {
  				this.togglePlaying()
  			}
  			// 歌词跟着跳转
  			if (this.currentLyric) {
  				// 毫秒单位
  				this.currentLyric.seek(currentTime * 1000)
  			}
  		},
  		end() {
  			// 播放结束切换下一首
  			if (this.mode === playMode.loop) {
  				this.loop()
  			} else {
	  			this.next() 				
  			}
  		},
  		// 单曲循环方法
  		loop() {
  			this.$refs.audio.currentTime = 0
  			this.$refs.audio.play()
  			if (this.currentLyric) {
  				// 回到一开始
  				this.currentLyric.seek(0)
  			}
  		},
  		getLyric() {
  			this.currentSong.getLyric().then((lyric) => {
          // this.currentSong.lyric会先获取到，再执行resolve(lyric),异步获取,会延迟，
          // currentsong.lyric只有一个,lyric可有多个，当2个不是同一个时，说明currentsong更新而lyric还没获取到
          // if (this.currentSong.lyric !== lyric) {
          //   return
          // }
  				this.currentLyric = new Lyric(lyric, this.handleLyric)
  				if (this.playing) {
  					this.currentLyric.play()
  				}
  			}).catch(() => {
  				// 异常时清空
  				this.currentLyric = null
  				this.playingLyric = ''
  				this.currentLineNum = 0
  			})
  		},
  		// 每句歌词改变时回调一次，传入lines对象当前的line
  		handleLyric({lineNum, txt}) {
        this.currentLineNum = lineNum
  			if (lineNum > 5) {
  				// 将当行歌词的前面第5行滚动到顶部，保持当行歌词在中间
  				// 将当前行的前第5行滚动到顶部
  				let lineEl = this.$refs.lyricLine[this.currentLineNum - 5]
  				this.$refs.lyricList.scrollToElement(lineEl, 1000)
  			} else {
  				this.$refs.lyricList.scrollTo(0, 0, 1000)
  			}
  			this.playingLyric = txt
  		},
  		// 中部左右滑动
  		middleTouchStart(e) {
  			console.log('start')
  			this.touch.initiated = true
  			const touch = e.touches[0]
  			// 存储页面位置
  			this.touch.startX = touch.pageX
  			this.touch.startY = touch.pageY
  		},
  		middleTouchMove(e) {
  			if (!this.touch.initiated) {
  				return
  			}
  			console.log('move')
  			const touch = e.touches[0]
  			const deltaX = touch.pageX - this.touch.startX
  			const deltaY = touch.pageY - this.touch.startY
  			// 如果纵向位移大于横向位移，则左右不移动，判断为歌词纵向滚动
  			if (Math.abs(deltaY) > Math.abs(deltaX)) {
  				return
  			}
  			// middle-r的最终左偏移量2种情况
  			const left = this.currentShow === 'cd' ? 0 : -window.innerWidth
  			// middle-r偏移过程的偏移量是当前位置+手指位移,且在[-window.innerwidth,0]间
  			const offsetWidth = Math.min(0, Math.max(-window.innerWidth, left + deltaX))
  			// 偏移比例
  			this.touch.percent = Math.abs(offsetWidth / window.innerWidth)
  			// 歌词偏移
  			this.$refs.lyricList.$el.style[transform] = `translate3d(${offsetWidth}px,0,0)`
  			// 在移动过程中不设过渡时间
        this.$refs.lyricList.$el.style[transitionDuration] = 0
        this.$refs.middleL.style.opacity = 1 - this.touch.percent
        this.$refs.middleL.style[transitionDuration] = 0
  		},
  		middleTouchEnd() {
  			console.log('end')
  			let offsetWidth
  			let opacity
  			// cd页面时
  			if (this.currentShow === 'cd') {
  				// 滑动比例大于10%，直接到达最终位置
  				if (this.touch.percent > 0.1) {
  					offsetWidth = -window.innerWidth
  					this.currentShow = 'lyric'
  					opacity = 0
  				} else {
  					offsetWidth = 0
  					opacity = 1
  				}
  			} else {
  				// lyric页面时
  				// 右滑比例大于0.1，即middle-r占屏宽小于90%
  				if (this.touch.percent < 0.9) {
  					offsetWidth = 0
  					this.currentShow = 'cd'
  					opacity = 1
  				} else {
  					offsetWidth = -window.innerWidth
  					opacity = 0
  				}
  			}
  			const time = 300
        this.$refs.lyricList.$el.style[transform] = `translate3d(${offsetWidth}px,0,0)`
        this.$refs.lyricList.$el.style[transitionDuration] = `${time}ms`
        this.$refs.middleL.style.opacity = opacity
        this.$refs.middleL.style[transitionDuration] = `${time}ms`
        this.touch.initiated = false
  		},
      showPlaylist() {
        this.$refs.playlist.show()
      },

  		// 补0
  		_pad(num, n = 2) {
  			let len = num.toString().length
  			while (len < n) {
  				num = '0' + num
  				len++
  			}
  			return num
  		},
  		// 获取偏移位置和放大倍数
  		_getPosAndScale() {
  			// mini's width, 中心的各边距
  			const targetWidth = 40
  			const paddingLeft = 40
  			const paddingBottom = 30
  			// normal上边距
  			const paddingTop = 80
  			// normal's width
  			const width = window.innerWidth * 0.8
  			// 缩小比例
  			const scale = targetWidth / width
  			// 偏移距离
  			const x = -(window.innerWidth / 2 - paddingLeft)
  			const y = window.innerHeight - paddingTop - width / 2 - paddingBottom
  			return {
  				x,
  				y,
  				scale
  			}
  		},
  		...mapMutations({
  			setFullScreen: 'SET_FULL_SCREEN'
  		}),
      ...mapActions([
        'savePlayHistory'
      ])
  	},
  	watch: {
  		currentSong(newSong, oldSong) {
        // 如果没有新歌曲
        if (!newSong.id) {
          return
        }
  			// 歌曲没改变时，保持原播放状态
  			if (newSong.id === oldSong.id) {
  				return
  			}
  			// 歌曲改变且有歌词时
  			if (this.currentLyric) {
  				// 停止歌词播放
  				this.currentLyric.stop()
  			}
        clearTimeout(this.timer)
  			// dom渲染后再播放
  			// 修复微信后台js不执行问题，重新进入后延时调用播放
  			// this.$nextTick(() => {
  			this.timer = setTimeout(() => {
  				this.$refs.audio.play()
          // 获取歌词是异步的
          this.getLyric()
  			}, 1000)
  		},
  		// playing改变，改变播放状态
  		playing(newPlaying) {
  			const audio = this.$refs.audio
  			this.$nextTick(() => {
	  			newPlaying ? audio.play() : audio.pause()
  			})
  		}
  	},
  	components: {
  		ProgressBar,
  		ProgressCircle,
  		Scroll,
      Playlist
  	}
  }
</script>

<style scoped lang="stylus">
	@import "~common/stylus/variable"
	@import "~common/stylus/mixin"

	.player
		.normal-player
			position: fixed
			left: 0
			right: 0
			top: 0
			bottom: 0
			z-index: 150
			background: $color-background
			.background
				position: absolute
				left: 0
				top: 0
				width: 100%
				height: 100%
				z-index: -1
				opacity: .6
				filter: blur(20px)
			.top
				position: relative
				margin-bottom: 25px
				.back
					position: absolute
					top: 0
					left: 6px
					z-index: 50
					.icon-back
						display: block
						padding: 9px
						font-size: $font-size-large-x
						color: $color-theme
						transform: rotate(-90deg)
				.title
					width: 70%
					margin: 0 auto
					line-height: 40px
					text-align: center
					no-wrap()
					font-size: $font-size-large
					color: $color-text
				.subtitle
					line-height: 20px
					text-align: center
					font-size: $font-size-medium
					color: $color-text
			.middle
				position: fixed
				width: 100%
				top: 80px
				bottom: 170px
				white-space: nowrap
				font-size: 0
				.middle-l
					display: inline-block
					vertical-align: top
					position: relative
					width: 100%
					height: 0
					padding-top: 80%
					.cd-wrapper
						position: absolute
						left: 10%
						top: 0
						width: 80%
						height: 100%
						.cd
							width: 100%
							height: 100%
							box-sizing: border-box
							border: 10px solid rgba(255, 255, 255, .2)
							border-radius: 50%
							&.play
								animation: rotate 20s linear infinite
							&.pause
								animation-play-state: paused
							.image
								position: absolute
								left: 0
								top: 0
								width: 100%
								height: 100%
								border-radius: 50%
					.playing-lyric-wrapper
						width: 80%
						margin: 30px auto 0 auto
						overflow: hidden
						text-align: center
						.playing-lyric
							height: 20px
							line-height: 20px
							font-size: $font-size-medium
							color: $color-text-l
				.middle-r
					display: inline-block
					vertical-align: top
					width: 100%
					height: 100%
					overflow: hidden
					.lyric-wrapper
						width: 80%
						margin: 0 auto
						overflow: hidden
						text-align: center
						.text
							line-height: 32px
							color: $color-text-l
							font-size: $font-size-medium
							&.current
								color: $color-text
			.bottom
				position: absolute
				bottom: 50px
				width: 100%
				.dot-wrapper
					text-align: center
					font-size: 0
					.dot
						display: inline-block
						vertical-align: middle
						margin: 0 4px
						width: 8px
						height: 8px
						border-radius: 50%
						background: $color-text-l
						&.active
							width: 20px
							border-radius: 5px
							background: $color-text-ll
				.progress-wrapper
					display: flex
					align-items: center
					width: 80%
					margin: 0px auto
					padding: 10px 0
					.time
						color: $color-text
						font-size: $font-size-small
						flex: 0 0 30px
						line-height: 30px
						width: 30px
						&.time-l
							text-align: left
						&.time-r
							text-align: right
					.progress-bar-wrapper
						flex: 1
				.operators
					display: flex
					align-items: center
					.icon
						flex: 1
						color: $color-theme
						&.disable
							color: $color-theme-d
						i
							font-size: 30px
					.i-left
						text-align: right
					.i-center
						padding: 0 20px
						text-align: center
						i
							font-size: 40px
					.i-right
						text-align: left
					.icon-favorite
						color: $color-sub-theme
			&.normal-enter-active, &.normal-leave-active
				transition: all .4s
				.top, .bottom
					transition: all .4s cubic-bezier(.86, .18, .82, 1.32)
			&.normal-enter, &.normal-leave-to
				opacity: 0
				.top
					transform: translate3d(0, -100px, 0)
				.bottom
					transform: translate3d(0, 100px, 0)
		.mini-player
			display: flex
			align-items: center
			position: fixed
			left: 0
			bottom: 0
			z-index: 180
			width: 100%
			height: 60px
			background: $color-highlight-background
			&.mini-enter-active, &.mini-leave-active
				transition: all 0.4s
			&.mini-enter, &.mini-leave-to
				opacity: 0
			.icon
				flex: 0 0 40px
				width: 40px
				padding: 0 10px 0 20px
				img
					border-radius: 50%
					&.play
						animation: rotate 10s linear infinite
					&.pause
						animation-play-state: paused
			.text
				display: flex
				flex-direction: column
				justify-content: center
				flex: 1
				line-height: 20px
				overflow: hidden
				.name
					margin-bottom: 2px
					no-wrap()
					font-size: $font-size-medium
					color: $color-text
				.desc
					no-wrap()
					font-size: $font-size-small
					color: $color-text-d
			.control
				flex: 0 0 30px
				width: 30px
				padding: 0 10px
				.icon-play-mini, .icon-pause-mini, .icon-playlist
					font-size: 30px
					color: $color-theme-d
				.icon-mini
					font-size: 32px
					position: absolute
					left: 0
					top: 0

	@keyframes rotate
		0%
			transform: rotate(0deg)
		100%
			transform: rotate(360deg)

</style>


